/* try to use mobile first strategy */
/* reset */
:root,
[data-theme=light] {
  --color-text: #222;
  --color-bg: #ccc;
  --color-bg-subtle: #ddd;
  --color-primary: #dc3545;
  --color-secondary: #20c997;
  --color-tertiary: #dc3545;
  --color-emphasis: #dc3545;
  --color-link: #222;
  --color-link-hover: #dc3545;
  --color-button-hover: #ddd;
  --color-border: #555;
}

[data-theme=dark] {
  --color-text: #eee;
  --color-bg: #111;
  --color-bg-subtle: #333;
  --color-primary: #dc3545;
  --color-secondary: #20c997;
  --color-tertiary: #dc3545;
  --color-emphasis: #dc3545;
  --color-link: #222;
  --color-link-hover: #dc3545;
  --color-button-hover: #222;
  --color-border: #555;
}

/**
* screen reader only
**/
.sr-only {
  border: 0 !important;
  clip: rect(1px, 1px, 1px, 1px) !important;
  -webkit-clip-path: inset(50%) !important;
  clip-path: inset(50%) !important;
  height: 1px !important;
  margin: -1px !important;
  overflow: hidden !important;
  padding: 0 !important;
  position: absolute !important;
  width: 1px !important;
  white-space: nowrap !important;
}

.sr-only-focusable:focus,
.sr-only-focusable:active {
  clip: auto !important;
  -webkit-clip-path: none !important;
  clip-path: none !important;
  height: auto !important;
  margin: auto !important;
  overflow: visible !important;
  width: auto !important;
  white-space: normal !important;
}

/**
* image
**/
.img-full-width {
  width: 100%;
}

/**
* text align
**/
.text-align-left {
  text-align: left;
}

.text-align-right {
  text-align: right;
}

.text-align-center {
  text-align: center;
}

.visually-hidden {
  position: absolute !important;
  width: 1px !important;
  height: 1px !important;
  padding: 0 !important;
  margin: -1px !important;
  overflow: hidden !important;
  clip: rect(0, 0, 0, 0) !important;
  white-space: nowrap !important;
  border: 0 !important;
}

.reset-list {
  padding: 0;
  margin: 0;
  list-style: none;
}

