body {
  background: var(--color-bg);
  color: var(--color-text);
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 0.875rem;
  font-weight: 400;
  line-height: 1.5;
}

h1,
h2,
h3,
h4,
h5,
h6,
.h1,
.h2,
.h3,
.h4,
.h5,
.h6 {
  margin: 0;
  padding: 0;
  color: inherit;
}

h1,
.h1 {
  font-size: 4.0625rem;
  font-weight: 300;
}

h2,
.h2 {
  font-size: 3.4375rem;
  font-weight: lighter;
}

h3,
.h3 {
  font-size: 2.5rem;
  font-weight: 700;
}

h4,
.h4 {
  font-size: 1.875rem;
  font-weight: 700;
}

h5,
.h5 {
  font-size: 1.25rem;
  font-weight: 700;
}

h6,
.h6 {
  font-size: 0.75rem;
  font-weight: 700;
}

p {
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
}

a {
  color: inherit;
}

.header {
  background-color: var(--color-bg-subtle);
  font-weight: 700;
  padding: 1rem 0;
}

.accordion {
  --accordion-text-color: var(--color-text);
  --accordion-bg-color: var(--color-bg);
  --accordion-border-color: var(--color-border);
  --accordion-trigger-hover-color: var(--color-button-hover);
  --accordion-icon-color: var(--color-border);
}

.accordion-trigger {
  position: relative;
  display: flex;
  flex-flow: row nowrap;
  align-items: center;
  justify-content: space-between;
  margin: 0;
  padding: 0.625rem 0.4375rem 0.625rem 0;
  width: 100%;
  border: 0;
  appearance: none;
  background-color: transparent;
  box-shadow: none;
  cursor: pointer;
  line-height: 1;
  text-align: left;
  text-decoration: none;
  font-weight: 700;
  font-size: 1.25rem;
  color: var(--accordion-text-color);
}
.accordion-trigger::after {
  content: "";
  position: absolute;
  width: 100%;
  bottom: 0;
  left: 0;
  height: 1px;
  background-color: var(--accordion-border-color);
}
@media (hover: hover) {
  .accordion-trigger:hover {
    background-color: var(--accordion-trigger-hover-color);
  }
  .accordion-trigger:hover::after {
    height: 5px;
  }
}
@media screen and (min-width: 992px) {
  .accordion-trigger {
    padding: 1.125rem 0.4375rem 1.125rem 0;
    font-size: 1.5rem;
  }
}
.accordion-trigger-label {
  position: relative;
  width: calc(100% - 100px);
  line-height: 1.15;
}
.accordion-trigger-icon {
  width: 1.5rem;
  height: 1.5rem;
  position: relative;
}
.accordion-trigger-icon::before {
  content: "";
  position: absolute;
  width: 0.25rem;
  height: 1.5rem;
  left: 0.625rem;
  top: 0;
  background-color: var(--accordion-icon-color);
  transform-origin: center;
  transform: rotate(0);
  transition: transform 0.3s;
}
.accordion-trigger-icon::after {
  content: "";
  position: absolute;
  width: 1.5rem;
  height: 0.25rem;
  top: 0.625rem;
  left: 0;
  background-color: var(--accordion-icon-color);
}
.accordion-trigger[aria-expanded=true] .accordion-trigger-icon::before {
  transform: rotate(90deg);
  left: 10px;
}

.accordion-panel {
  position: relative;
  height: 0;
  top: 1px;
  outline: none;
}
.accordion-panel > div {
  position: relative;
  padding-top: 1.5rem;
  padding-bottom: 2.5rem;
  padding-right: 10%;
  opacity: 0;
}
.accordion-panel > div::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  height: 1px;
  width: 100%;
  background: transparent;
}
.accordion-panel.expanded > div {
  opacity: 1;
}

.accordion-panel.animate-in {
  transition: height 0.3s ease-out;
}
.accordion-panel.animate-in > div {
  transition: opacity 0.4s ease-out 0.2s;
}
.accordion-panel.animate-out {
  transition: height 0.3s ease-out 0.2s;
}
.accordion-panel.animate-out > div {
  transition: opacity 0.3s ease-out;
}

.button {
  display: inline-block;
  border: none;
  margin: 0;
  text-decoration: none;
  cursor: pointer;
  line-height: 1;
  box-shadow: none;
  appearance: none;
  border-radius: 3px;
  background-color: #eee;
  color: #222;
  padding: 0.8125rem 0.625rem;
  font-size: 0.875rem;
  font-family: "Roboto", sans-serif;
  font-weight: 600;
  border: 1px solid black;
}
.button:hover {
  background-color: rgba(238, 238, 238, 0.6);
}
.button:focus {
  outline: 2px solid var(--color-blue);
}
.button.disabled, .button[disabled] {
  background-color: #ddd;
  border: 1px solid #aaa;
  color: #aaa;
  cursor: default;
}

.card-img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  object-position: center;
}
.card-link, .card-link h2 {
  text-decoration: none;
}

.tab-list {
  margin-top: 2rem;
}

.tab-btn[aria-selected=true] {
  background-color: white;
  border: 2px solid black;
}
.tab-btn[disabled=true] {
  cursor: not-allowed;
}

.tab-panel {
  display: none;
  padding-top: 2rem;
}
.tab-panel.is-visible {
  display: block;
}

.modal {
  position: fixed;
  display: none;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 9000;
  justify-content: center;
  align-items: center;
}
.modal.is-visible {
  display: flex;
}

.modal-dialog {
  position: relative;
  background-color: white;
  width: 90%;
  height: 50%;
  padding: 2rem;
  box-sizing: border-box;
}

.charts-table {
  width: 100%;
  color: #111;
  border-collapse: collapse;
}
.charts-table th, .charts-table td {
  padding: 13px 5px;
  font-size: 12px;
  font-weight: 400;
}
.charts-table thead {
  border-bottom: 4px solid #111;
}
.charts-table thead th {
  text-align: center;
}
.charts-table thead tr th:first-child {
  text-align: left;
}
.charts-table tbody {
  border-bottom: 4px solid #0d6efd;
}
.charts-table tbody tr {
  border-bottom: 1px solid #bbb;
}
.charts-table tbody tr th {
  text-align: left;
  color: #0d6efd;
}
.charts-table tbody tr td {
  text-align: center;
}
.charts-table tfoot th {
  text-align: left;
}
.charts-table tfoot td {
  text-align: center;
}
.charts-table .active {
  background-color: rgba(60, 218, 195, 0.2);
}

#chartjs-tooltip table {
  padding: 8px;
  background-color: white;
  box-shadow: 1px 1px 4px 1px rgba(0, 0, 0, 0.24);
  color: #222;
  font-weight: normal;
}
#chartjs-tooltip th {
  padding-bottom: 5px;
  border-bottom: 1px solid #bbb;
  text-align: left;
  font-weight: 400;
  line-height: 1.2;
  font-size: 12px;
}
#chartjs-tooltip tbody {
  font-size: 21px;
}
#chartjs-tooltip td {
  text-align: left;
  padding-top: 3px;
  font-weight: 400;
  line-height: 1.2;
}

.hero {
  height: 100vh;
  background: #000;
  color: var(--color-bg);
  display: flex;
  justify-content: left;
  align-items: center;
}
.hero h2 {
  font-size: 200px;
}
.hero .line1 {
  padding-left: 50%;
}

.sticky {
  position: relative;
}

.sticky-bar {
  width: 100%;
  height: 0;
  position: sticky;
  top: calc(100% - 30px);
  z-index: 1000;
}

.sticky-bar-orange {
  background-color: var(--color-primary);
  position: absolute;
  width: 1px;
  height: 30px;
  top: 0;
  left: 0;
}

.sticky-bar-black {
  background-color: var(--color-bg);
  position: absolute;
  width: 100%;
  height: 30px;
  top: 0;
  left: 0;
}

.slide {
  position: relative;
  background: var(--color-bg);
}

.bg-sticky {
  background-color: var(--color-bg);
  color: var(--color-text);
  font-size: 12px;
  height: 100vh;
  width: 100%;
  position: sticky;
  top: 0;
  overflow: hidden;
}

.slide1-slide {
  height: 100vh;
  width: 100%;
  color: var(--color-text);
  font-size: 120px;
  position: relative;
  font-weight: bold;
  display: flex;
  align-items: center;
}

.slide2 {
  padding: 100px 0;
  overflow: hidden;
}

.text {
  font-size: 100px;
  line-height: 1.3;
  font-weight: bold;
  color: var(--color-text);
  transform-origin: left center;
}

.slide3 {
  position: relative;
}

.slide3-slide {
  position: sticky;
  height: 100vh;
  width: 100%;
  font-size: 100px;
  overflow: hidden;
  top: 0;
  color: var(--color-text);
  text-align: center;
  font-weight: bold;
  display: flex;
  align-items: center;
  justify-content: center;
}

.slide3-pattern {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.mosaic1 {
  height: 15%;
  position: absolute;
  left: 0;
  top: 0;
  opacity: 0;
  transition: opacity ease-out 0.2s;
}

.slide3-img {
  height: 100vh;
  background-color: rgba(168, 243, 161, 0.1);
}

.slide4-slide {
  font-size: 100px;
  font-weight: bold;
  color: var(--color-text);
  height: 100vh;
  display: flex;
  align-items: center;
}

.slide4-content {
  width: 60%;
  margin-top: -300px;
}

.slide4-sticky {
  width: 30%;
  position: sticky;
  top: 0;
  margin-left: 70%;
  font-size: 60px;
  color: var(--color-text);
  padding: 100px 0;
}

.beyond {
  min-height: 700px;
  padding-top: 100px;
}

.footer {
  background-color: var(--color-bg);
  position: fixed;
  width: 100%;
  bottom: 0;
  left: 0;
  padding: 1rem 0;
  font-size: 10px;
}

