.modal {
  position: fixed;
  display: none;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  z-index: 9000;
  justify-content: center;
  align-items: center;

  &.is-visible {
    display: flex;
  }
}

.modal-dialog {
  position: relative;
  background-color: $white;
  width: 90%;
  height: 50%;
  padding: $spacer * 2;
  box-sizing: border-box;
}

